@import '../../style/variables.scss';

.skills__grid-item {
    // background-color: rgb(0, 255, 255, 0.4);
    // border: solid 2px aqua;
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
    overflow: visible;
    font-weight: 700;
    font-size: 18px;

    &:nth-child(1) {
        grid-column: 1 / 5;
        grid-row: 1;
        transform: rotateZ(-30 + (random($limit: 60) * 1deg));
        scale: 0;

        @keyframes scaleUp {
            0% {
                opacity: 0;
                scale: 0;
            }

            10% {
                opacity: 1;
            }

            100% {
                scale: 1.0;
            }
        }

        &.appear {
            animation: scaleUp 300ms cubic-bezier(0, .2, .88, .63) 400ms forwards;
        }
    }

    &:nth-child(2) {
        grid-column: 6 / 8;
        grid-row: 1 / 3;
        transform: rotateZ(-30 + (random($limit: 60) * 1deg));
        scale: 0;

        &.appear {

            animation: scaleUp 200ms cubic-bezier(0, .2, .88, .63) 600ms forwards;
        }
    }

    &:nth-child(3) {
        grid-column: 1/4;
        grid-row: 2 / 5;
        transform: rotateZ(-30 + (random($limit: 60) * 1deg));
        scale: 0;

        &.appear {
            animation: scaleUp 200ms cubic-bezier(0, .2, .88, .63) 800ms forwards;
        }
    }

    &:nth-child(4) {
        grid-column: 4 / 9;
        grid-row: 3/5;
        transform: rotateZ(-30 + (random($limit: 60) * 1deg));
        scale: 0;

        &.appear {
            animation: scaleUp 200ms cubic-bezier(0, .2, .88, .63) 500ms forwards;
        }
    }

    &:nth-child(5) {
        grid-column: 1/4;
        grid-row: 4;
        transform: rotateZ(-30 + (random($limit: 60) * 1deg));
        scale: 0;

        &.appear {
            animation: scaleUp 200ms cubic-bezier(0, .2, .88, .63) 700ms forwards;
        }
    }

    &:nth-child(6) {
        grid-column: 7/10;
        grid-row: 4/7;
        transform: rotateZ(-30 + (random($limit: 60) * 1deg));
        scale: 0;

        &.appear {
            animation: scaleUp 200ms cubic-bezier(0, .2, .88, .63) 900ms forwards;
        }
    }

    &:nth-child(7) {
        grid-column: 3/5;
        grid-row: 1/4;
        transform: rotateZ(-30 + (random($limit: 60) * 1deg));
        scale: 0;

        &.appear {
            animation: scaleUp 200ms cubic-bezier(0, .2, .88, .63) 1100ms forwards;
        }
    }

    &:nth-child(8) {
        grid-column: 2/4;
        grid-row: 5/7;
        transform: rotateZ(-30 + (random($limit: 60) * 1deg));
        scale: 0;

        &.appear {
            animation: scaleUp 200ms cubic-bezier(0, .2, .88, .63) 1200ms forwards;
        }
    }

    &:nth-child(9) {
        grid-column: 4/6;
        grid-row: 4/6;
        transform: rotateZ(-30 + (random($limit: 60) * 1deg));
        scale: 0;

        &.appear {
            animation: scaleUp 200ms cubic-bezier(0, .2, .88, .63) 1000ms forwards;
        }
    }

    &:nth-child(10) {
        grid-column: 4/8;
        grid-row: 6;
        transform: rotateZ(-30 + (random($limit: 60) * 1deg));
        scale: 0;

        &.appear {
            animation: scaleUp 200ms cubic-bezier(0, .2, .88, .63) 1300ms forwards;
        }
    }

    &:nth-child(11) {
        grid-column: 6/10;
        grid-row: 1/5;
        transform: rotateZ(-30 + (random($limit: 60) * 1deg));
        scale: 0;

        &.appear {
            animation: scaleUp 200ms cubic-bezier(0, .2, .88, .63) 1400ms forwards;
        }
    }
}